.wordCenter{
  text-align: center;
}

.storyMapScreen{
  height: calc(75vh - 3rem);
  display: flex;
}

.storyChat{
  height: 25vh;
  overflow: auto;
  background-color: rgb(0,0,0,0.8);
  color:yellow
}


.battleScreen{
  width:80%;
  border: 1px solid black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-image: url('../img/Background/Grass5.png');
  background-repeat: round;
}

.titleName{
  -webkit-text-stroke: 0.2px black;
  stroke: 0.2px black;
  color: white;
}


.StoryHUD{
  width:20%;
  border: 1px solid black;
  background: rgb(220,220,220);
  background: -moz-linear-gradient(142deg, rgba(220,220,220,1) 0%, rgba(186,219,225,1) 75%, rgba(155,218,230,1) 87%, rgba(119,216,236,1) 96%, rgba(0,212,255,1) 100%);
  background: -webkit-linear-gradient(142deg, rgba(220,220,220,1) 0%, rgba(186,219,225,1) 75%, rgba(155,218,230,1) 87%, rgba(119,216,236,1) 96%, rgba(0,212,255,1) 100%);
  background: linear-gradient(142deg, rgba(220,220,220,1) 0%, rgba(186,219,225,1) 75%, rgba(155,218,230,1) 87%, rgba(119,216,236,1) 96%, rgba(0,212,255,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#dcdcdc",endColorstr="#00d4ff",GradientType=1);
}

.imgFlip{
  transform: scaleX(-1);
  height: 50px;
}
.imgFlipRocker{
  height: 150px;
  width: 80px;
}

.onHitAnimate{
  animation-name: OnHit;
  animation-duration: 1.0s;
}

@keyframes OnHit {
0%{ opacity: 1;}
20%{ opacity: 0;}
30%{opacity: 1;}
50%{ opacity: 0; }
60%{opacity: 1;}
100%{opacity: 1;}
}

.userSkillBox{
  display: flex;
  flex-direction: column;
}

.HUDBox{
  display: flex;
  align-items: center;
}

.BarBasicHUD{
	height: 10px;
  border: 1px solid black;
  background-color: #E2E9E3;
  border-radius: 12px;
  width: 130px;
}

.expBarBasicHUD{
  height: 8px;
  width: 90%;
  border-radius: 0px;
  margin: 0 5% 0;
}

.clockBarBasicHUD{
  border-radius: 5px;
}

.basicStatsHUD{
  background-color: #A6C1E4;
  border: 1px solid #6983A8;
}

.leveltextHUD{
  border-radius: 5px;
  margin: 10px 0 10px;
  padding: 3px 0 3px;
}
.UserImageBox{
  position: relative;
}
.greenHP,
.redHP,
.yellowHP,
.blueSP,
.purpleHP{
	height: 6px;
  border: 1px solid black;
  background-color: #463C57;
  display: block;
  width:80%;
}
.greenHP,
.redHP,
.yellowHP{
  position: absolute;
  top:150px;
  left:50%;
  transform: translate3d(-50%,0,0);
}
.blueSP{
  position: absolute;
  top:156px;
  left:50%;
  transform: translate3d(-50%,0,0);
}
.userNamePosition{
  position: absolute;
  top:162px;
  left:50%;
  transform: translate3d(-50%,0,0);
}
progress.greenHP::-webkit-progress-bar,
progress.redHP::-webkit-progress-bar,
progress.yellowHP::-webkit-progress-bar,
progress.blueSP::-webkit-progress-bar,
progress.purpleHP::-webkit-progress-bar{  
	background-color: #463C57;
}
progress.BarBasicHUD::-webkit-progress-bar{
  background-color: #FEFEFF;
  border-radius: 12px;
}

/* Unfortunately, It doesn't work if I combine them together (nice in CSS) */
.greenHP::-webkit-progress-value{
	background-color: rgb(56, 219, 92)
}
.greenHP::-moz-progress-bar{  
	background-color: rgb(56, 219, 92)
}
.yellowHP::-webkit-progress-value{  
	background-color: #B6B72A
}
.yellowHP::-moz-progress-bar{  
	background-color: #B6B72A
}
.redHP::-webkit-progress-value{  
	background-color: #B91514
}
.redHP::-moz-progress-bar{  
	background-color: #B91514
}
.purpleHP::-webkit-progress-value{  
	background-color: #DD13F3
}
.purpleHP::-moz-progress-bar{  
	background-color: #DD13F3
}
.blueSP::-webkit-progress-value{  
	background-color: #1D5DE7
}
.blueSP::-moz-progress-bar{  
	background-color: #1D5DE7
}

.BarBasicHUD::-webkit-progress-value{
  background-color:#CBDAF9;
  border-radius: 12px;
  background: linear-gradient(145deg, #b7c4e0, #cce1ff);
  box-shadow:  10px 10px 80px #adb9d4,
             -10px -10px 60px #c2d4ed;
}
.BarBasicHUD::-moz-progress-bar{
  background-color:#CBDAF9;
  border-radius: 12px;
  background: linear-gradient(145deg, #b7c4e0, #cce1ff);
  box-shadow:  10px 10px 80px #adb9d4,
             -10px -10px 80px #c2d4ed;
}
.expBarBasicHUD::-webkit-progress-value{
  background-color:#6587BE;
  border-radius: 0px;
  background: #6587BE;
  box-shadow: none;
}
.expBarBasicHUD::-moz-progress-bar{
  background-color:#6587BE;
  border-radius: 0px;
  background: #6587BE;
  box-shadow: none;
}
.clockBarBasicHUD::-webkit-progress-value{
  border-radius: 5px;
  background: #6587BE;
  box-shadow: none;
}
.clockBarBasicHUD::-moz-progress-bar{
  border-radius: 5px;
  background: #6587BE;
  box-shadow: none;
}

.nameStatsHUD{
  font-size: 20px;
}
.destextHUD{
  margin: 0 10px 0;
}
.hptextHUD,
.sptextHUD,
.hpPerHUD,
.spPerHUD{
  margin: 0 5px 0;
}

.zenytextHUD{
  text-align: right;
  padding: 0 5px 0;
  border-bottom: 1px solid black;
}

.hptextHUD,
.sptextHUD{
  width: 21px;
}

.altanImg{
  height: 180px;
}

.enemyBox{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.clockHUD{
  text-align: left;
}


/* Story HUD Button */
.goGoButtonSkills,
.goGoButton{
  position: relative;
  height:35px;
  background-color: #A6C1E4;
  color:#363f4b;
  transition: color 0.07s ease-in-out;

}
.goGoButton:hover,
.goGoButtonSkills:hover{
  background-color: #363f4b;
  color:#A6C1E4;
  transition: color 0.07s ease-in-out;
}

.goGoButtonSkills:hover p{
  opacity: 0;
}
.goGoButtonSkills:hover span{
  opacity: 1;
}
.goGoButtonFig{
  opacity: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
}
.goGoButton::before,
.goGoButtonSkills::before{
  left:0;
  right: 90%;
  bottom: 0;
  background-color: #363f4b;
  height:1px;
  position: absolute;
  content: '';
  z-index: 2;
  transition: right 0.35s ease-out;
}
.goGoButton:hover::before,
.goGoButtonSkills:hover::before{
  z-index: -1;
  right: 0;
  transition: right 0.35s ease-out;
}
.goGoButton::after,
.goGoButtonSkills::after{
  left:0;
  bottom: 0;
  right: 100%;
  background-color: #A6C1E4;
  height:1px;
  position: absolute;
  content: '';
  transition: right 0.35s ease-out;
}
.goGoButton:hover::after,
.goGoButtonSkills:hover::after{
  right: 0;
  transition: right 0.35s ease-out;
}

.goGoButtonName{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%,-50%,0);
  transform-origin: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.goGoButtonSkillBash{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%,-50%,0);
  transform-origin: 50%;
  opacity: 0;
  transition: all 0.1s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.insufficentSP{
  color: rgb(202, 14, 14)
}


@media (max-width: 1199px){
  .BarBasicHUD{
    width: 100px;
  }
  .expBarBasicHUD{
    width: 80%;
  }
}

@media (max-width: 1023px){
  .BarBasicHUD{
    width: 50px;
  }
  .expBarBasicHUD{
    width: 80%;
  }

}
@media (max-width: 767px){
  .hptextHUD,
  .sptextHUD,
  .hpPerHUD,
  .spPerHUD,
  .classTitle,
  .clockName{
    display: none;
  }
  .BarBasicHUD{
    width: 70px;
    margin: 0 5px 5px;
  }
  .HUDBox{
    justify-content: center;
  }
  .leveltext{
    font-size: 14px;
  }
  .greenHP,
  .redHP,
  .yellowHP,
  .blueSP,
  .purpleHP{
    width: 85%;
  }
}
@media (max-width: 467px){
  .BarBasicHUD{
    width: 50px;
  }
  .expBarBasicHUD{
    margin: 0;
  }
  .nameStatsHUD,
  .zenytextHUD{
    margin: 0;
    text-align: center;
  }
  .basicStatsHUD{
    font-size: 12px;
  }
  .clockBarBasicHUD{
    margin: 5px 0 0;
  }
  .userSkillBox{
    font-size: 14px;
  }
  .altanImg{
    height: 120px;
  }
  .greenHP,
  .redHP,
  .yellowHP{
    top:100px;

  }
  .blueSP{
    top:106px;
  }
  .userNamePosition{
    top:112px;
  }
  .imgFlip{
    height: 35px;
  }
  .imgFlipRocker{
    height: 100px;
    width: 53px;
  }
  .leveltextHUD{
    text-align: center;
  }
}