.gameOptionTitle,
.gameOptionSelect{
    text-align: center;
}

.gameOptionTitle{
    height: 25vh;
    line-height: 25vh;
    font-family: 'Chelsea Market', cursive;
    color: white;
    -webkit-text-stroke: 1.2px black;
    stroke: 1.2px black;
    text-shadow: 3px 3px 2px #486896;
    font-size: 40px;
}
.gameOptionSelect{
    height: calc(75vh - 3rem);
    line-height: 40px;
}

.gameOptionBackground{
    background-image: url('../img/Background/Sound.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.gameOptionBGMBox{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}

.gameOptionWord{
    font-size: 25px;
    text-shadow: 2px 2px 2px #657c9c;
    margin: 0px 10px 0px;
    color: white;
    font-family: 'Sniglet', cursive;
}

.gameOptionNumber{
    width:40px;
}

.menuButton,
.gameOptionButton{
    cursor: pointer;
    position: relative;
    text-decoration: none;
    font-size: 25px;
    color: black;
    font-family: 'Sniglet', cursive;
    background: linear-gradient(110deg, rgba(220,220,220,1) 0%, rgba(186,219,225,1) 100%);
    border: solid #7CAFFF 1px;
    border-radius: 5px;
    padding: 20px;
    text-shadow: 2px 2px 2px #657c9c;
    box-shadow: 6px 6px 7px #0D2444;
    margin-bottom: 10px;
    -webkit-transition: all 0.1s;
    -moz-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
  }

.gameOptionButton{
    padding: 5px 20px 5px;
    margin-bottom: 0;
}
.menuButton:hover,
.gameOptionButton:hover{
  text-shadow: 1px 1px 1px #657c9c;
  box-shadow: 3px 3px 3px #0D2444;
}

.menuButton::before,
.menuButton::after{
  position: absolute;
  top: 50%;
  width: 10px;
  height: 10px;
  border: 1px solid black;
  background-color: #764abc;
  content: '';
  transition: all 0.1s;
  transform: translate3d(0,-50%,0) rotate3d(0,0,1,-45deg) scale3d(0,0,1);
  opacity: 0;
  transform-origin: 50%;
}

.menuButton:hover::before,
.menuButton:hover::after{
  transform: translate3d(0,-50%,0) rotate3d(0,0,1,-45deg) scale3d(1,1,1);
  opacity: 1;
}
.menuButton::before{
  left: 5px;
}
.menuButton::after{
  right: 5px;
}

